import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('homeView')
export class homeView extends Component {

    @property(Node)
    discoverPage: Node = null
    @property(Node)
    invitePage: Node = null
    @property(Node)
    earnPage: Node = null
    @property(Node)
    drawPage: Node = null

    pageArray: Node[] = [
    ]
    
    start() {
        this.pageArray = [
            this.discoverPage,
            this.invitePage,
            this.earnPage,
            this.drawPage
        ]
    }

    shiftCurrentPage(btn, data: string){
        console.log('======',data);
        (this.pageArray[data] as Node).setPosition(0,0)
    }

    update(deltaTime: number) {
        
    }
}

